header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 300px;
  background-color: #00739c;
}

.posts {
  margin-top: 100px;
  display: flex;
  align-items: center;
  flex-direction: column;
}

.post a {
  text-decoration: none;
  color: #0c0c0c;
  font-size: 30px;
  cursor: pointer;
  transition: linear 0.2s;
}

.post a:hover {
  color: red;
}

.phone-number {
  text-decoration: none;
  color: #0c0c0c;
  cursor: pointer;
}

.post-image img {
  max-width: 500px;
  height: auto;
}
.single-post {
  margin: 100px auto;
}
.color {
  width: 10%;
}